import Vue from 'vue'
import Router from 'vue-router'
// 路由
Vue.use(Router)

const routes = [{
    path: '/Login',
    name: 'Login',
    component: () => import('@/views/Login')
  }, {
    path: '/',
    redirect: '/home',
    name: 'Container',
    component: () => import('@/Container/Container'),
    children: [{
        path: 'home',
        name: '首页',
        icon: 'el-icon-news',
        component: () => import('@/views/home')
      },
      {
        path: 'student',
        name: '学生信息',
        icon: 'el-icon-edit',
        component: () => import('@/views/student')
      },
      {
        path: 'student/information',
        hidden: true,
        component: () => import('@/views/student/information')
      },
      {
        path: 'question',
        name: '问卷',
        icon: 'el-icon-tickets',
        component: () => import('@/views/question')
      },
      {
        path: 'group',
        name: '工作坊',
        redirect: '/',
        icon: 'el-icon-menu',
        component: () => import('@/views/group'),
        children: [{
          path: '/',
          name: '活动分类',
          component: () => import('@/views/group/act-group')
        }, {
          path: 'activity',
          name: '活动',
          component: () => import('@/views/group/activity')
        }]
      },
      {
        path: 'setting',
        name: '设置',
        icon: 'el-icon-setting',
        component: () => import('@/views/setting'),
      },
    ]
  },
  {
    path: '*',
    redirect: '/'
  }
]

const router = new Router({
  base: '/',
  mode: 'hash', //default-->hash
  routes: routes
})

//无登录情况的重定向
router.beforeEach((to, from, next) => {
  let isLogin = JSON.parse(sessionStorage.getItem('isLogin'))
  //console.log(to)
  //console.log(from)
  if (isLogin && to.path === '/login') {
    if (confirm('确认退出登录?')) {
      localStorage.clear()
      sessionStorage.clear()
      next()
    } else {
      next(false)
    }
  } else if (!isLogin && to.path != '/login') {
    next('/login')
  } else next()
})

export default router



