import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

const state = { //全局访问的数据
  userInfo: {
    userName: localStorage.getItem("name"),
    token: localStorage.getItem("USERTK")
  },
  actGroup: {
    data: "",
    prototype: {
      groupId: "",
      groupName: "",
      groupIcon: "",
      groupAddTime: "",
      groupShow: true
    },
    pageNum: "1"
  },
  activity: {
    data: "",
    prototype: {
      activityId: "",
      activityName: "",
      activityInformation: "",
      activityPersonNum: null,
      activityStart: "",
      activityEnd: "",
      activitySignUp: "",
      activityShow: true,
      studioId: null
    },
    pageNum: "1"
  },
  student: {
    data: "",
    pageNum: "1"
  }
}
const getters = {
  //活动分类
  getGroupList: state => {
    return state.actGroup.data.list
  },
  getGroupPrototype: state => {
    return state.studio.prototype
  },
  getGroupPage: state => {
    return state.studio.pageNum
  },
  //活动
  getActivityList: state => {
    return state.activity.data.list
  },
  getActivityPrototype: state => {
    return state.activity.prototype
  },
  getActivityPage: state => {
    return state.activity.pageNum
  },
  //学生信息
  getStudentList: state => {
    return state.student.data.list
  },
  getStudentPage: state => {
    return state.student.pageNum
  }
}
const mutations = {
  changePageNum: function (state, item) {
    item.name == "group" ?
      state.actGroup.pageNum = item.num :
      state.activity.pageNum = item.num
  },
  pushGroup: function (state, data) {
    state.actGroup.data = data
  },
  pushActivity: function (state, data) {
    state.activity.data = data
  },
  pushStudent: function (state, data) {
    state.student.data = data
  }
}
const actions = {
  pushGroup(context, data) {
    context.commit("pushGroup", data.data)
    context.commit("changePageNum", {
      name: data.name,
      num: data.num
    })
  },
  pushActivity(context, data) {
    context.commit("pushActivity", data.data)
    context.commit("changePageNum", {
      name: data.name,
      num: data.num
    })
  },
  pushStudent(context, data) {
    context.commit("pushStudent", data.data)
    // context.commit("changePageNum", {
    //   name: data.name,
    //   num: data.num
    // })
  }
}
const store = new Vuex.Store({
  state,
  getters,
  mutations,
  actions
})

export default store



