//api路径
//axios配置
import axios from 'axios';
axios.defaults.baseURL = 'https://pang.yank-tenyond.cn/'; //定义域名
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'; // 配置请求头
axios.defaults.headers.common['Authorization'] = localStorage.getItem("USERTK"); //添加token
axios.defaults.timeout = 5000; //请求超时
//登录
export const getVerifyCode = key =>{ //获取验证码
  return axios.get('admin/captcha?key=' + key).then(res => res.data);
}
export const requestLogin = params => { //登录
  return axios.post('admin/login', params).then(res => res.data);
};
export const getAdminLog = params => { //获取登录日志
  return axios.post('admin/log', {
    params: params
  }).then(res => res.data);
};
//活动分类
export const getAllGroup = params => { //查找所有活动分类
  return axios.get('activity-group', {
    params: params
  }).then(res => res.data);
};
export const getGroup = id => { //查找活动分类byID
  return axios.get('activity-group/' + id).then(res => res.data);
};
export const pushGroup = params => { //修改活动分类
  return axios.put('activity-group', params).then(res => res.data);
};
export const addGroup = params => { //添加活动分类
  return axios.post('activity-group', params).then(res => res.data);
};
export const delGroup = id => { //删除活动分类
  return axios.delete('activity-group/' + id).then(res => res.data);
};
//活动
export const getAllActivity = params => { //查找所有活动
  return axios.get('activity', {
    params: params
  }).then(res => res.data);
};
export const getActivity = id => { //查找活动byID
  return axios.get('activity/' + id).then(res => res.data);
};
export const addActivity = params => { //添加活动
  return axios.post('activity', params).then(res => res.data);
};
export const pushActivity = params => { //修改活动
  return axios.push('activity', params).then(res => res.data);
};
export const delActivity = id => { //删除活动
  return axios.delete('activity/' + id).then(res => res.data);
};
//学生用户
export const getAllUser = params => { //查找所有用户
  return axios.get('user', {
    params: params
  }).then(res => res.data);
}
export const addUser = params => { //添加用户
  return axios.post('user', {
    params: params
  }).then(res => res.data);
}
export const pushUser = params => { //修改用户信息
  return axios.put('user', {
    params: params
  }).then(res => res.data);
}
export const getUserByAct = params => { //查找用户by活动ID
  return axios.get('user/activity/' + params.id, {
    params: params.page
  }).then(res => res.data);
}
export const getUserByOpen = params => { //查找用户byOpenID
  return axios.post('user/openId', {
    params: params
  }).then(res => res.data);
}
export const getUser = id => { //查找用户byUserID
  return axios.get('user/' + id).then(res => res.data);
}
//活动-活动分类
export const getGroupAct = params => { //查看活动分组下的活动
  return axios.get('activity/activity-group/' + params.id, {
    params: params.page
  }).then(res => res.data);
};
//活动-用户
export const getUserAct = params => { //查看用户参加的活动
  return axios.get('activity/user/' + params.id, {
    params: params.page
  }).then(res => res.data);
};
export const userAddAct = params => { //用户加入活动
  return axios.post('activity/user/', {
    params: params
  }).then(res => res.data);
};
export const userDelAct = params => { //用户退出活动
  return axios.delete('activity/user/', {
    params: params
  }).then(res => res.data);
};
//标签
export const getAllTag = params => { //查找所有标签
  return axios.get('tag', {
    params: params
  }).then(res => res.data)
};
export const getTag = id => { //查找标签byID
  return axios.get('tag/' + id).then(res => res.data)
};
export const addTag = params => { //添加标签
  return axios.post('tag', {
    params: params
  }).then(res => res.data)
};
export const pushTag = params => { //修改标签
  return axios.put('tag', {
    params: params
  }).then(res => res.data)
};
export const delTag = params => { //删除标签
  return axios.delete('tag', {
    params: params
  }).then(res => res.data)
};
export const getActTag = id => { //查看活动标签
  return axios.get('tag/activity/' + id).then(res => res.data)
};
export const getUserTag = id => { //查看用户标签
  return axios.get('tag/user/' + id).then(res => res.data)
};
//问卷
export const getAllQuestion = () => { //查找所有问题
  return axios.get('question').then(res => res.data)
};
export const isAnswer = id => { //是否回答问卷
  axios.get('answer/' + id).then(res => {
    return res.data.data
  })
}
//雷达图
export const getUserScore = id => { //查询用户雷达图全部因素
  return axios.get('aspect/score/' + id).then(res => res.data)
}



